.page_wrap {
	margin-top: rem(32);

	&__info {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(40);

		.count {
			display: flex;
			flex-wrap: wrap;

			span {
				font-weight: bold;

				&:last-child {
					margin-left: rem(10);
					color: var(--c-accent);
				}
			}
		}
	}


	.review__item {
		padding: rem(12) 0;
		
		border-bottom: rem(1) solid #E5E8EC;

		margin-bottom: rem(40);

		&:nth-child(1n+3) {
			.image {
				background-color: #C2E2D7;
			}
		}

		&:nth-child(2n+3) {
			.image {
				background-color: #FFEFE1;
			}
		}

		&:first-child {
			border-top: rem(1) solid #E5E8EC;
		}
	}

	.page_nav {
		margin-bottom: rem(64);
	}
}