.header {
    width: 100%;
    height: 100vh;
    background: url(../img/header_bg_x1_.jpg) no-repeat top center;
    background-size: cover;
    >.container {
        display: flex;
        flex-direction: column;
        height: 100%;
    }
    &__picture {
        // display: none;
    }
    &__top {
        // position: sticky;
        // top: 0;
        margin-top: rem(20);
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        .logo {
            display: block;
            width: rem(131);
            margin-top: rem(2);
        }
        .header_bay {
            .button_primary {
                display: none;
            }
            i {
                font-size: rem(22);
            }
        }
    }
    &__bottom {
        position: relative;
        margin-top: auto;
        padding-bottom: rem(100);
        display: flex;
        flex-direction: column;
        align-items: center;
        .abs {
            position: absolute;
            top: 0;
            left: 0;
        }
        .title_abs {
            margin-top: rem(170);
        }
        h1 {
            margin-bottom: rem(24);
            text-align: center;
            font-weight: normal;
            max-width: rem(352);
            margin-left: auto;
            margin-right: auto;
        }
        p {
            font-size: rem(18);
            margin-bottom: rem(40);
            text-align: center;
        }
    }
    &_menu {
        &__list,
        .header_menu__child {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: white;
            z-index: 10;
            padding-top: rem(80);
            padding-left: rem(30);
            padding-right: rem(30);
            display: none;
            .first {
                // li {
                // 	&:nth-child(2) {
                // 		border-bottom: rem(1) solid #E5E8EC;
                // 	}
                // }
                li.active {
                    border-bottom: rem(1) solid #E5E8EC;
                }
            }
            &-right {
                display: none;
            }
            ul {
                li {
                    padding: rem(15) 0;
                    a {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        font-size: rem(14);
                        color: var(--c-dark);
                        line-height: 144%;
                        font-weight: 500;
                        &:hover {
                            color: var(--c-accent);
                        }
                        i {
                            font-size: rem(32);
                        }
                    }
                }
                .desktop {
                    display: none;
                }
                .active {
                    padding-bottom: rem(26);
                    margin-bottom: rem(8);
                    a {
                        color: var(--c-accent);
                    }
                }
            }
        }
    }
    &_burger {
        width: rem(32);
        height: rem(32);
        display: flex;
        justify-content: center;
        align-items: center;
        &__wrap {
            width: rem(20);
            height: rem(12);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .line,
            &::before,
            &::after {
                width: 100%;
                height: rem(2);
                border-radius: rem(1);
                background-color: var(--c-dark);
                transition: all 200ms ease;
            }
            &::before,
            &::after {
                content: "";
            }
        }
    }
}

.header_fixed {
    .container {
        &::before {
            content: "";
            height: rem(76);
            width: 100%;
        }
    }
    .header__top {
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        margin-top: 0;
        padding: rem(20);
        z-index: 10;
        background-color: white;
    }
}

.header_menu_active {
    .header_burger,
    .logo,
    .header_bay {
        position: relative;
        z-index: 11;
    }
    .header_burger {
        &__wrap {
            &::before,
            &::after {
                position: absolute;
                top: rem(14);
                left: 0;
            }
            &::before,
            &::after,
            .line {
                border-radius: 0;
            }
            &::before {
                transform: rotate(45deg);
            }
            &::after {
                transform: rotate(-45deg);
            }
            .line {
                opacity: 0;
            }
        }
    }
    .header_menu__list {
        display: block;
        height: 100%;
        overflow-y: auto;
    }
}

.header_menu_active_child {
    .header_burger {
        &__wrap {
            &::before,
            &::after {
                position: absolute;
                left: 0;
            }
            &::before,
            &::after,
            .line {
                border-radius: rem(1);
                width: rem(10);
            }
            &::before {
                top: rem(10);
                left: rem(8);
                transform: rotate(-45deg);
            }
            &::after {
                top: rem(16);
                left: rem(8);
                transform: rotate(45deg);
            }
            // .line {
            // 	opacity: 0;
            // }
        }
    }
    .header_menu__child {
        display: block;
        height: 100%;
        overflow-y: auto;
    }
}