.news {
	background-color: var(--c-light-blue);

	padding: 0 0 rem(64) 0;

	&__top {
		position: relative;
		margin-bottom: rem(24);

		span {
			position: absolute;
			left: 0;
			bottom: 0;
			font-weight: bold;
			font-size: rem(14);
			text-transform: uppercase;
			padding: rem(5) rem(20);
			background-color: #FFC200;
		}

		img {
			width: 100%;
			// height: 100%;
			// object-fit: cover;
			vertical-align: middle;
		}
	}

	&__bottom {
		margin: 0 rem(20);

		display: flex;
		flex-direction: column;

		h2 {
			margin-bottom: rem(8);
		}

		p {
			margin-bottom: rem(40);
		}

		.button_primary {
			// width: auto;
			align-self: center;
			height: auto;
			padding: rem(11) rem(40);
		}
	}
}