.you_know {
    position: relative;
    background-color: var(--c-dirty-blue);
    height: rem(441);
    margin-top: rem(218);
    &__wrap {
        .image {
            position: absolute;
            top: rem(-156);
            left: 0;
            width: rem(267);
            height: rem(250);
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .text {
            padding-top: rem(142);
            span {
                display: block;
                font-size: rem(18);
                line-height: 152%;
                letter-spacing: rem(1.5);
                margin-bottom: rem(27);
                text-transform: uppercase;
            }
            h3 {
                font-size: rem(24);
                line-height: 150%;
            }
        }
    }
}