.slider {
    padding: rem(64) 0;
    h2 {
        margin-bottom: rem(28);
        text-align: center;
        max-width: rem(264);
        margin-left: auto;
        margin-right: auto;
    }
    .swiper-container {}
    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        &-image {
            margin-bottom: rem(20);
        }
        h3 {
            margin-bottom: rem(8);
        }
        p {
            text-align: center;
            max-width: rem(294);
        }
    }
}

.swiper-page {
    margin-top: rem(40);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .swiper-pagination-bullet {
        margin-left: 0 !important;
        margin-right: rem(14) !important;
        &:last-child {
            margin-right: 0;
        }
    }
    .swiper-pagination-bullet-active {
        background-color: var(--c-accent);
    }
}