.reviews_col {
	// background-color: var(--c-accent);
	padding: rem(36) 0 rem(34) 0;
	background: url(../img/page_goods_about/reviews_bg.jpg) no-repeat top center;
	background-size: cover;

	>.container {
		display: flex;
		flex-direction: column;
	}

	&__left {
		// width: rem(400);
		margin-bottom: rem(34);

		h3 {
			font-family: $font-mplus;
			font-size: rem(22);
			line-height: 144%;
			font-weight: 500;
			color: white;
			margin-bottom: rem(24);
		}

		.links {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			a {
				color: white;
				font-size: rem(12);
				font-weight: 500;
				line-height: 130%;
				border-bottom: rem(1.5) solid white;

				&:hover {
					border-color: transparent;
				}
			}
		}
	}

	&__right {
		// margin-left: rem(30);

		.swiper-nav {
			margin-top: rem(22);
			height: rem(20);
		}
	}
}