.sertifications_accordion {
    background-color: var(--c-light-blue);
    padding: rem(64) 0;
    display: flex;
    flex-wrap: wrap;
    .w-7\/12 {
        flex: 0 0 100%;
        max-width: 100%;
    }
    &__left {
        flex: 0 0 100%;
        max-width: 100%;
        h2 {
            display: none;
        }
    }
    &__right {
        display: none;
    }
}

.accordion {
    &__item {
        // margin-bottom: rem(28);
        &:last-child {
            // margin-bottom: 0;
        }
        .title {
            cursor: pointer;
            padding: rem(30) 0;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
            border-bottom: rem(1) solid var(--c-dark);
            span {
                font-size: rem(14);
                font-weight: 500;
                line-height: 152%;
                text-transform: uppercase;
                letter-spacing: rem(1);
            }
            button {
                width: rem(28);
                height: rem(28);
                display: flex;
                align-items: center;
                justify-content: center;
                &::before {
                    content: "\e90c";
                    // transition: all 150ms ease;
                    font-family: 'icons';
                    color: var(--c-dark);
                    font-size: rem(28);
                }
            }
        }
        .text {
            margin-top: rem(10);
            // display: none;
            height: 0;
            opacity: 0;
            pointer-events: none;
            transition: all 300ms ease;
            p {
                font-size: rem(14);
                font-weight: 300;
                line-height: 191%;
                font-family: $font-merri;
                margin-bottom: rem(10);
                &:last-of-type {
                    margin-bottom: 0;
                }
            }
            b {
                font-size: rem(13);
                font-weight: 500;
                line-height: 144%;
                display: block;
                margin-bottom: rem(5);
                text-transform: uppercase;
            }
            a {
                display: inline-block;
                margin-top: rem(48);
                font-weight: 500;
                font-size: rem(12);
                text-decoration: underline;
                &:hover {
                    text-decoration: none;
                }
            }
        }
    }
    &__item.active {
        .title {
            button {
                &::before {
                    content: "\e916";
                    font-size: 1.95px;
                }
            }
        }
        .text {
            // display: block;
            height: 100%;
            opacity: 1;
            pointer-events: visible;
        }
    }
}