.popup_overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(48, 56, 75, 0.86);
	backdrop-filter: blur(4px);

	z-index: 250;
	padding: rem(64) 0;

	display: none;

	&__close {
		position: absolute;
		top: 0;
		right: 0;
		width: rem(32);
		height: rem(32);

		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(28);
		}
	}

	&__wrap {
		position: relative;
		width: calc(100% - 40px);
		margin: 0 rem(20);
		border-radius: rem(5);
		background-color: white;
		height: 100%;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		h3 {
			font-weight: normal;
			margin-bottom: rem(12);
		}

		> p {
			text-align: center;
			max-width: rem(250);
			margin-left: auto;
			margin-right: auto;
		}
	}
}

.popup_active {
	display: block;
}


.popup_modify {
	height: 100%;

	padding: rem(64) 0;

	.popup_overlay__wrap {
		height: 100%;

		// width: 100%;

		padding: 0 rem(45);

		h3 {
			text-align: center;
			margin-bottom: rem(36);
		}

		.items {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;

			.item {
				display: flex;
				align-items: center;
				justify-content: center;
				width: calc((100% - 2rem) / 2);
				height: rem(32);
				margin-right: rem(1);
				margin-bottom: rem(24);

				&:nth-child(2),
				&:nth-child(4),
				&:last-child {
					margin-right: 0;
				}

				&:last-child {
					margin-bottom: 0;
					margin-right: 0;
				}

				img {
					display: block;
				}
			}
		}
	}
}

.popup_review {
	overflow-y: auto;
	.popup_overlay__wrap {
		width: auto;
		display: block;
		height: auto;

		.popup_header {
			background-color: var(--c-light-blue);
			padding: rem(14) rem(28);

			display: flex;
			flex-direction: column;
			align-items: center;

			margin-bottom: rem(40);

			span {
				font-size: rem(18);
				font-weight: bold;
				letter-spacing: rem(1.5);
				margin-bottom: rem(10);
			}

			p {
				color: var(--c-paragraph);
				max-width: 100%;
			}
		}

		.popup_form {
			display: flex;
			flex-direction: column;
			padding: 0 rem(34);

			.form_rating {
				margin-left: auto;
				margin-right: auto;
				margin-bottom: rem(14);

			}

			.field_set,
			.field_file,
			.field_recaptcha {
				margin-bottom: rem(10);
			}

			.button_secondary {
				height: rem(42);
				padding: 0;
				font-size: rem(14);
				margin-bottom: rem(36);
			}
		}
	}

	> .text {
		margin-top: rem(12);
		margin-left: rem(20);
		margin-right: rem(20);

		font-weight: 300;
		font-size: rem(12);
		color: white;

		a {
			border-bottom: rem(1) dashed white;
		}
	}
}

.form_rating {
	// width: rem(124);
	overflow: hidden;

	&:not(:checked) > input {
		display: none;
	}
	&:not(:checked) > label {
		width: rem(20);
		float: right;
		cursor: pointer;
		margin-right: rem(6);
	}
	&:not(:checked) > label::before {
		content: "\e912";
		font-family: 'icons';
		font-size: rem(22);
		color: #B4BCC4;
	}

	> input:checked ~ label::before {
		color: #EF8F76;
	}

	&:not(:checked) > label:hover,
	&:not(:checked) > label:hover ~ label {
		&::before {
			color: #EF8F76;
		}
	}

	> input:checked + label:hover,
	> input:checked + label:hover ~ label,
	> input:checked ~ label:hover,
	> input:checked ~ label:hover ~ label,
	> label:hover ~ input:checked ~ label {
		&::before {
			color: #EF8F76;
		}
	}
}