#header_blog {
	height: rem(253);
	background: url(../img/page_blog/header_bg_x1.jpg) no-repeat top center;
	background-size: cover;

	.header__bottom {
		margin-top: rem(40);
		padding-bottom: 0;

		h1 {
			font-weight: normal;
			margin-bottom: rem(16);
		}

		p {
			font-size: rem(13);
			color: var(--c-paragraph);
			max-width: rem(322);

			br {
				display: none;
			}
		}
	}
}