.other_product {
    background-color: var(--c-light-blue);
    padding: rem(64) 0;
    .swiper-nav {
        button {
            i {
                &::before {
                    color: #30384B;
                }
            }
        }
    }
    .swiper-wrapper {
        .swiper-slide {
            &:first-child {
                .product_item {
                    &__image {
                        &::before {
                            background-color: var(--c-dirty-blue);
                        }
                    }
                }
            }
            &:nth-child(2) {
                .product_item {
                    &__image {
                        &::before {
                            background-color: #E8F2EB;
                        }
                    }
                }
            }
            &:nth-child(3) {
                .product_item {
                    &__image {
                        &::before {
                            background-color: #FFF2D4;
                        }
                    }
                }
            }
            &:last-child {
                .product_item {
                    &__image {
                        &::before {
                            background-color: #FFE1E1;
                        }
                    }
                }
            }
        }
    }
    h3 {
        text-align: center;
        font-size: rem(24);
        margin-bottom: rem(12);
    }
    p {
        margin-left: auto;
        margin-right: auto;
        max-width: rem(300);
        font-size: rem(18);
        text-align: center;
        margin-bottom: rem(24);
    }
    .product_item {
        padding: 0;
        border: 0;
        &__image {
            margin-bottom: rem(25);
            img {
                width: rem(200);
                margin-top: rem(8);
            }
            &::before {
                width: rem(220);
                height: rem(220);
                margin-left: rem(-110);
                top: 0;
            }
        }
        &__text {
            h3 {
                font-size: rem(14);
            }
        }
    }
    .button_primary {
        display: none;
    }
}