.sertifications {
    background-color: var(--c-dirty-blue);
    padding: rem(64) 0;
    >.container {
        display: flex;
        flex-direction: column;
    }
    h2 {
        margin-bottom: rem(48);
        text-align: center;
    }
    &__items {
        display: flex;
        flex-direction: column;
        margin-bottom: rem(40);
        .item {
            margin-bottom: rem(36);
            display: flex;
            flex-direction: column;
            align-items: center;
            &:last-child {
                margin-bottom: 0;
            }
            &__image {
                width: rem(132);
                height: rem(132);
                // border-radius: 50%;
                // display: flex;
                // align-items: center;
                // justify-content: center;
                // background-color: var(--c-light-blue);
                margin-bottom: rem(20);
                img {}
            }
            &__info {
                text-align: center;
                h3 {
                    margin-bottom: rem(12);
                }
            }
        }
    }
    .button_primary {
        align-self: center;
        height: auto;
        padding: rem(11) rem(15);
    }
}