.product_accordion {
	padding: rem(64) 0;

	&__left {
		.accordion {
			border-top: rem(1) solid var(--c-dark);

			&__item {
				&:last-child {
					.title {
						border-bottom: 0;
					}
				}
			}
		}
		h3 {
			margin-bottom: rem(20);
			font-size: rem(24);
		}
	}

	&__right {
		display: none;
	}

	
}