.page_blog {
    .sect_nav {
        ul {
            // column-count: 2;
            // display: block;
            // @include min340 {
            //     justify-content: space-between;
            // }
            li {
                margin-right: rem(18);
                // margin-right: 0;
                @include min768 {
                    flex: 0 0 calc(25% - 1.125rem);
                }
                &:nth-child(4n) {
                    margin-right: 0;
                }
                // flex: 0 0 20%;
                @include min768 {
                    &:first-child {
                        flex: 0 0 calc(30% - 1.125rem);
                    }
                    &:nth-child(2) {
                        flex: 0 0 calc(35% - 1.125rem);
                    }
                    &:nth-child(3) {
                        flex: 0 0 calc(18% - 1.125rem);
                    }
                    &:nth-child(4) {
                        flex: 0 0 calc(18% - 1.125rem);
                    }
                }
                @include min340 {
                    flex: 0 0 calc(50% - 1.125rem) !important;
                    margin-right: 0 !important;
                    &:nth-child(2n) {
                        // flex: 0 0 calc(35% - 1.125rem);
                        display: flex;
                        justify-content: flex-end;
                    }
                }
            }
        }
    }
    .articles {
        padding: rem(36) 0 0 0;
        &__items {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .page_nav {
        margin-top: 0;
        padding: rem(48) 0 rem(64) 0;
    }
}

.page_blog.position_scroll {
    position: relative;
    &::before {
        content: "";
        height: rem(117);
        width: 100%;
        display: block;
    }
}

.default_subscribe {
    .subscribe__form {
        margin-bottom: rem(52);
        .title {
            font-weight: 500;
            display: block;
            margin-bottom: rem(8);
        }
        .field {
            border-color: #E5E8EC;
            width: 75%;
            padding-right: rem(72);
        }
        .button_secondary {
            position: absolute;
            top: 0;
            right: 0;
            padding: rem(11) rem(30);
        }
    }
    .form_send_success {
        .field_set {
            opacity: 0;
        }
        .form_success {
            top: rem(24);
            display: flex;
            p {
                color: var(--c-dark);
            }
            button {
                i {
                    color: var(--c-dark);
                    &::before {
                        color: var(--c-dark);
                    }
                }
            }
        }
    }
}