.sect_nav {
    padding: rem(30) 0 rem(20) 0;
    border-bottom: rem(1) solid #E5E8EC;
    ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        li {
            margin-bottom: rem(14);
            margin-right: rem(25);
            a {
                font-weight: bold;
                &:hover {
                    color: var(--c-accent);
                }
            }
        }
    }
}

.sect_nav.scroll-active {
    position: fixed;
    // top: rem(114);
    left: 0;
    width: 100%;
    background-color: white;
    z-index: 10;
}

.page_questions {
    .sect_nav {
        ul {
            li {
                &:nth-child(2),
                &:last-child {
                    margin-right: 0;
                }
                &:nth-child(3) {
                    order: 1;
                }
                &:first-child {
                    order: 2;
                }
                &:nth-child(2) {
                    order: 3;
                }
                &:nth-child(4) {
                    order: 5;
                }
                &:nth-child(5) {
                    order: 4;
                }
                &:last-child {
                    order: 6;
                }
            }
        }
    }
    &__sect {
        padding: 0 0 rem(42) 0;
        display: flex;
        flex-direction: column;
        &:first-child {
            padding: rem(42) 0;
        }
        >.title {
            text-align: center;
            font-weight: bold;
            font-size: rem(12);
            text-transform: uppercase;
            border-bottom: rem(2) solid var(--c-accent);
            margin-left: auto;
            margin-right: auto;
            letter-spacing: rem(1);
            margin-bottom: rem(46);
        }
        .item {
            margin-bottom: rem(36);
            &:last-child {
                margin-bottom: 0;
            }
            h3 {
                font-weight: bold;
                font-size: rem(24);
                font-family: $font-mplus;
                margin-bottom: rem(36);
            }
            .accordion {
                border-top: rem(1) solid #E5E8EC;
                &__item.active {
                    .title {
                        span {
                            text-decoration: underline;
                            color: var(--c-accent);
                        }
                    }
                }
                &__item {
                    .title {
                        padding: rem(23) 0;
                        border-color: #E5E8EC;
                        span {
                            letter-spacing: normal !important;
                            color: var(--c-accent);
                            font-weight: bold;
                            font-size: rem(14);
                            text-transform: none;
                        }
                        button {
                            &::before {
                                color: var(--c-accent);
                            }
                        }
                    }
                    .text {
                        p {
                            font-size: rem(14);
                        }
                    }
                }
            }
        }
    }
}

.feedback_faq {
    &::before,
    &::after {
        display: none;
    }
    .feedback__form-header {
        height: auto;
        padding: rem(37) 0;
        h2 {
            font-size: rem(18);
        }
    }
}