.field_set {
	position: relative;

	.field_error {
		position: absolute;
		bottom: rem(17);
		left: rem(13);
		color: #EA374C;
		font-size: rem(10);
	}

	.field_error_icon {
		position: absolute;
		top: 50%;
		right: rem(10);
		margin-top: rem(-8);
		width: rem(16);
		height: rem(16);
		border-radius: 50%;
		background-color: #C84741;

		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "\e905";
			font-family: 'icons';
			color: white;
			font-size: rem(14);
		}
	}

	.field_success_icon {
		position: absolute;
		top: 50%;
		right: rem(10);
		margin-top: rem(-8);
		width: rem(16);
		height: rem(16);
		border-radius: 50%;
		background-color: #60C468;

		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "\e913";
			font-family: 'icons';
			color: white;
			font-size: rem(7);
		}
	}
}

.field_set {
	textarea.field~.field_success_icon,
	textarea.field~.field_error_icon {
		top: rem(18);
	}
}

.field {
	width: 100%;
	font-size: rem(14);
	color: var(--c-paragraph);
	background-color: white;
	padding: rem(11) rem(12);
	border-radius: rem(4);
	box-shadow: 0;

	transition: all 300ms ease;

	border: rem(1) solid #E5E8EC;
	appearance: none;

	&:hover {
		border-color: #ADADAD;
	}

	&:active, &:focus {
		// border-color: #282B2E;
		border-color: #ADADAD;
	}

	@include placeholder {
		color: #ADADAD;
	}
}

textarea.field {
	resize: vertical;
}

.field_file {
	display: flex;
	align-items: center;
	input {
		display: none;
	}

	label {

		cursor: pointer;
		height: rem(46);
		display: flex;
		// flex-wrap: wrap;
		align-items: center;

		background-color: #E5E8EC;
		border-radius: rem(4);
		padding: 0 rem(10);

		margin-right: rem(10);
		// width: rem(146);

		.elipse {
			width: rem(16);
			height: rem(16);
			border-radius: 50%;

			margin-right: rem(6);

			display: flex;
			align-items: center;
			justify-content: center;
			background-color: var(--c-dark);

			i {
				&::before {
					color: white;
					font-size: rem(24);
				}
			}
		}

		span {
			color: black;
			font-size: rem(13);
			white-space: nowrap;
		}
	}

	p {
		text-align: left !important;
		max-width: rem(95) !important;
		font-size: rem(10);
		color: black;
		line-height: 152%;
	}
}

.field_recaptcha {
	padding: rem(8) rem(13);

	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;

	border: rem(1) solid #E5E8EC;

	

	&__left {
		.checked {
			display: flex;
			align-items: center;

			input {
				display: none;

				&:checked ~ .check {
					&::before {
						opacity: 1;
					}
				}
			}
			
			.check {
				position: relative;
				width: rem(26);
				height: rem(26);
				border: rem(1) solid #E5E8EC;
				background-color: white;
				display: block;

				margin-right: rem(16);

				&::before {
					content: "\e918";
					font-family: 'icons';
					color: #649E41;
					font-size: rem(24);

					opacity: 0;
				}
			}

			.text {
				font-family: 'Arial', sans-serif;
			}
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
		align-items: center;

		button {
			margin-bottom: rem(5);
		}

		span {
			color: #4D4D4D;
			font-family: 'Arial', sans-serif;
		}
	}
}