.questions {
    padding: rem(64) 0;
    &__left {
        margin-bottom: rem(36);
        h3 {
            font-size: rem(24);
        }
    }
    &__right {
        .accordion {
            border-top: rem(1) solid #E5E8EC;
            &__item {
                .title {
                    padding: rem(23) 0;
                    border-color: #E5E8EC;
                    span {
                        color: var(--c-accent);
                        font-weight: bold;
                        font-size: rem(14);
                        text-transform: none;
                        max-width: 80%;
                    }
                    button {
                        &::before {
                            color: var(--c-accent);
                        }
                    }
                }
                .text {
                    p {
                        font-size: rem(14);
                    }
                }
            }
        }
    }
}