.page_article {
    .w-8\/12,
    .w-4\/12 {
        flex: 0 0 100%;
        max-width: 100%;
    }
    .article {
        margin-bottom: rem(52);
        h1 {
            font-size: rem(28);
            line-height: 135%;
            margin-bottom: rem(16);
            font-weight: 500;
            font-family: $font-mplus;
        }
        h2 {
            margin-bottom: rem(12);
        }
        img {
            margin-bottom: rem(12);
            &:first-of-type {
                margin-bottom: rem(32);
            }
            width: 100%;
            object-fit: cover;
        }
        p {
            font-size: rem(14);
            line-height: 178%;
            font-family: $font-merri;
            margin-bottom: rem(32);
            font-weight: 300;
            a {
                color: var(--c-accent);
                text-decoration: underline;
                &:hover {
                    text-decoration: none;
                }
            }
        }
        &__share {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
            padding: rem(32) 0;
            border-top: rem(1) solid #E5E8EC;
            border-bottom: rem(1) solid #E5E8EC;
            h3 {
                font-weight: bold;
                font-family: $font-mplus;
                font-size: rem(13);
                color: rgba(29, 37, 44, .62);
                text-transform: uppercase;
            }
            &-links {
                display: flex;
                flex-wrap: wrap;
                a {
                    margin-right: rem(16);
                    &:last-child {
                        margin-right: 0;
                    }
                    i {
                        font-size: rem(36);
                    }
                }
            }
        }
        &__foot {
            margin-top: rem(32);
            .item {
                margin-bottom: rem(32);
                &:last-child {
                    margin-bottom: 0;
                }
                a {
                    font-family: $font-merri;
                    color: var(--c-accent);
                    display: inline-block;
                    margin-bottom: rem(8);
                    &:hover {
                        text-decoration: underline;
                    }
                }
                ol {
                    li {
                        list-style: decimal;
                        font-size: rem(13);
                        line-height: 160%;
                        font-weight: 500;
                        margin-left: rem(16);
                    }
                }
                p {
                    font-size: rem(13);
                    line-height: 160%;
                    font-weight: 500;
                    font-family: $font-mplus;
                }
            }
        }
    }
    .subscribe__form {
        padding-top: rem(48);
        .form_success {
            top: rem(70);
        }
    }
    .aside {
        margin-top: rem(48);
        background-color: var(--c-light-blue);
        margin-left: rem(-20);
        margin-right: rem(-20);
        padding-left: rem(20);
        padding-right: rem(20);
        display: flex;
        flex-direction: column;
        .button_primary {
            padding: rem(11) rem(28);
            height: rem(43);
            align-self: center;
            margin-bottom: rem(64);
        }
        >h3 {
            font-size: rem(12);
            font-weight: bold;
            font-family: $font-mplus;
            text-transform: uppercase;
            border-bottom: rem(2) solid var(--c-accent);
            margin-top: rem(38);
            margin-bottom: rem(28);
            align-self: center;
        }
        .articles__items {
            display: block;
            width: 100%;
            .item {
                width: 100%;
                background-color: white;
                border-bottom: 0;
                &__bottom {
                    padding: 0 rem(16);
                }
            }
        }
        .products__items {
            width: 100%;
            margin-top: rem(64);
            .swiper-wrapper,
            .swiper-slide {
                height: auto;
            }
            .item {
                flex: 0 0 100%;
                max-width: 100%;
                &__image {
                    &::before {
                        top: 0;
                        background-color: white;
                    }
                    img {
                        margin-top: rem(12);
                    }
                }
            }
            .swiper-nav {
                margin-top: rem(24);
            }
        }
        .swiper-nav {
            margin-top: rem(4);
            height: rem(20);
            button {
                i {
                    &::before {
                        color: #30384B;
                    }
                }
            }
        }
    }
}